<template>
  <div class="edited_note">
    <input
      type="text"
      class="title"
      v-model="title"
      placeholder="Enter new note title..."
    />
    <ol>
      <li v-for="(task, index) in tasks" :key="index">
        <input
          type="text"
          v-model="tasks[index].text"
          placeholder="Enter new task text..."
        />
      </li>
    </ol>
    <div class="modal-footer">
      <button @click="AddTask">
        Add task
      </button>
      <button @click="Add">Add note</button>
      <button><router-link to="/">Cancel</router-link></button>
    </div>
  </div>
</template>

<script>
import { mapMutations } from "vuex";

export default {
  name: "AddNoteWindow",
  data() {
    return {
      title: "",
      tasks: [],
    };
  },
  methods: {
    ...mapMutations(["addNote"]),
    Add() {
      this.addNote({
        title: this.title,
        tasks: this.tasks,
      });
      this.$router.push("/");
    },
    AddTask() {
      this.tasks.push({ text: "", isCompleted: false });
    },
  },
};
</script>
