<template>
  <div class="edited_note">
    <input
      type="text"
      class="title"
      v-model="ttl"
      placeholder="Enter new note title..."
    />
    <ol>
      <li v-for="(task, index) in tsk" :key="index">
        <textarea
          rows="1"
          v-model="tsk[index].text"
          placeholder="Enter new task text..."
          :class="{ completed: tsk[index].isCompleted }"
        />
        <input
          type="checkbox"
          @click="tsk[index].isCompleted = !tsk[index].isCompleted"
          v-model="tsk[index].isCompleted"
        />
      </li>
    </ol>
    <div class="modal-footer">
      <button @click="AddTask">
        Add task
      </button>
      <button @click="Save">
        Save
      </button>
      <button @click="Remove">
        Remove
      </button>
      <button @click="Discard">
        Cancel
      </button>
    </div>
    <ModalWindow
      v-if="ModalWindowVisibility"
      @hide="setModalWindowVisibility(false)"
    >
      <template v-slot:question v-if="isRemove">
        Are you sure you want remove note?
      </template>
      <template v-slot:question v-else>
        Are you sure you want to cancel changes?
      </template>
      <template v-slot:actions v-if="isRemove">
        <div class="buttons">
          <button @click="RemoveNote">Yes</button>
          <button @click="setModalWindowVisibility(false)">No</button>
        </div>
      </template>
      <template v-slot:actions v-else>
        <div class="buttons">
          <button @click="Cancel">Yes</button>
          <button @click="setModalWindowVisibility(false)">No</button>
        </div>
      </template>
    </ModalWindow>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { mapMutations } from "vuex";
import ModalWindow from "@/components/ModalWindow.vue";

export default {
  name: "EditNoteWindow",
  components: {
    ModalWindow,
  },
  computed: {
    ...mapGetters(["getIndex", "ModalWindowVisibility"]),
  },
  data() {
    return {
      ttl: this.$store.state.Note.notes[this.$store.state.Note.itemIndex].title,
      tsk: this.$store.state.Note.notes[this.$store.state.Note.itemIndex].tasks,
      isRemove: false,
    };
  },
  methods: {
    ...mapMutations([
      "addTask",
      "saveNote",
      "removeNote",
      "restoreNotes",
      "setModalWindowVisibility",
    ]),
    Cancel() {
      this.restoreNotes();
      this.setModalWindowVisibility(false);
      this.Hide();
    },
    Hide() {
      this.$router.push("/");
    },
    Save() {
      this.saveNote({ title: this.ttl, tasks: this.tsk });
      this.Hide();
    },
    AddTask() {
      this.addTask({ text: "", isCompleted: false });
    },
    Remove() {
      this.isRemove = true;
      this.setModalWindowVisibility(true);
    },
    Discard() {
      this.isRemove = false;
      this.setModalWindowVisibility(true);
    },
    RemoveNote() {
      this.removeNote(this.getIndex);
      this.setModalWindowVisibility(false);
      this.Hide();
    },
  },
};
</script>
